.root {
  border-radius: 3px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border: 1px solid;
  min-width: 18px; }

.primary {
  color: white;
  background: #008380;
  border-color: #008380; }

.secondary {
  color: rgba(0, 0, 0, 0.72);
  background: rgba(0, 0, 0, 0.08);
  border-color: transparent; }

.small {
  padding: 0 4px; }

.large {
  padding: 4px 7px;
  white-space: nowrap;
  border: none; }

.text {
  display: inline-flex;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px; }

.circle {
  border-radius: 9999px;
  min-height: 18px; }
