.root {
  position: relative; }

.normal {
  height: 18px;
  width: 18px; }

.large {
  height: 22px;
  width: 22px; }

.box {
  outline: none;
  appearance: none;
  opacity: 0;
  cursor: pointer; }

.checkmark {
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #fff;
  border-radius: 2px;
  border: 1px solid rgba(0, 0, 0, 0.12); }

.box:checked ~ .checkmark {
  border-color: #008380;
  background-color: #008380; }

.checkmark:after {
  content: '';
  position: absolute;
  display: none; }

.box:checked ~ .checkmark:after {
  display: block; }

.checkmark:after {
  border: solid white;
  border-width: 0 2px 2px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg); }

.checkmark.normal:after {
  left: 5px;
  top: 1px;
  width: 6px;
  height: 12px; }

.checkmark.large:after {
  left: 7px;
  top: 2px;
  width: 6px;
  height: 12px; }

.hoverCheckmark {
  background-color: rgba(0, 131, 128, 0.2); }

.disabled {
  border-color: rgba(0, 0, 0, 0.04);
  background-color: rgba(0, 0, 0, 0.04); }

.box:checked ~ .checkmark.disabled {
  opacity: 0.37; }
