.root {
  position: relative;
  width: max-content; }

.container {
  display: flex; }

.containerLeft {
  justify-content: flex-start;
  align-items: flex-end; }

.containerRight {
  justify-content: flex-end;
  align-items: flex-end; }

.containerCenter {
  justify-content: center;
  align-items: center; }

.containerMiddle {
  position: absolute;
  top: 50%;
  padding-left: 100%; }

.tooltips {
  align-self: center;
  position: absolute;
  z-index: 999999; }

.tooltipsTop {
  composes: tooltips;
  padding-bottom: 6px;
  bottom: calc(100% + 4px); }

.tooltipsRight {
  composes: tooltips;
  padding-left: 6px;
  left: calc(100% + 4px); }

.tooltipsBottom {
  composes: tooltips;
  padding-top: 6px;
  top: calc(100% + 4px); }

.tooltipsLeft {
  composes: tooltips;
  padding-right: 6px;
  right: calc(100% + 4px); }

.tooltipsAlignTop {
  top: 0; }

.tooltipsAlignBottom {
  bottom: 0; }

.content {
  width: max-content;
  background: #292929;
  border-radius: 3px;
  padding: 0.75rem 0.75rem 0.75rem 0.75rem;
  box-shadow: 0 16px 44px -12px rgba(26, 26, 29, 0.3); }

.arrow {
  position: absolute;
  width: 0;
  height: 0;
  border-color: transparent;
  border-style: solid; }

.arrowPositionTop {
  border-width: 0px 6px 6px 6px;
  border-bottom-color: #292929;
  top: 0px; }

.arrowPositionRight {
  border-width: 6px 0px 6px 6px;
  border-left-color: #292929;
  right: 0px; }

.arrowPositionBottom {
  border-width: 6px 6px 0px 6px;
  border-top-color: #292929;
  bottom: 0px; }

.arrowPositionLeft {
  border-width: 6px 6px 6px 0px;
  border-right-color: #292929; }

.arrowAlignLeft {
  left: 12px;
  margin-left: -6px; }

.arrowAlignCenter {
  left: 50%;
  margin-left: -6px; }

.arrowAlignRight {
  right: 12px;
  margin-right: -6px; }

.arrowAlignMiddle {
  top: calc(50% - 6px);
  margin-left: -6px; }

.arrowAlignTop {
  top: 12px;
  margin-left: -6px; }

.arrowAlignBottom {
  bottom: 12px;
  margin-left: -6px; }
