.imageCaption {
  color: white;
  padding: 0.75rem 0.75rem 0.75rem 0.75rem; }

.image {
  width: 200px;
  height: 200px; }

.maskedImage {
  width: 96px;
  height: 96px;
  margin: 0 1.5rem 0 0; }

.previewMask {
  display: inline-flex; }

.imagePreview {
  height: 300px;
  margin-bottom: 24px;
  cursor: pointer; }

.sideTab {
  display: inline-flex;
  width: 100%; }

.tabPanels {
  max-width: 80%;
  min-width: 80%;
  width: 80%;
  flex: 2; }

.tabList {
  flex: 1;
  min-width: 30%; }

.tab {
  padding: 8px 0px;
  width: fit-content;
  text-decoration: none;
  background-image: linear-gradient(180deg, #008380 30%, #008380 30%);
  background-size: 0% 1.5px;
  background-repeat: no-repeat;
  background-position: 0 100%;
  transition: all 0.12s;
  cursor: pointer; }
  .tab:hover {
    background-size: 100% 1.5px;
    text-shadow: 2px 0 white, -2px 0 white, 0 2px white, 0 -2px white, 2px 0 white, -2px 0 white, 2px 0 white, -2px 0 white, 2px 0 white, -2px 0 white, 2px 0 white, -2px 0 white;
    color: #00514F; }

.special {
  padding: 24px; }

.appearance {
  appearance: searchfield; }

.bannerPosition {
  position: fixed;
  bottom: 0px;
  left: 0;
  right: 0; }

.radioClass {
  margin: 0.75rem 0 0.75rem 0; }

.markClass {
  margin-right: 0.75rem; }

.labelClass {
  cursor: pointer; }

.checkmark {
  margin: 0 0.75rem 0 0; }

.checkbox {
  display: block; }

.markRight {
  display: flex;
  justify-content: space-between;
  margin: 2rem 0 2rem 0;
  padding: 0.75rem 0 0.75rem 0;
  border-top: 1px solid rgba(0, 0, 0, 0.12);
  border-bottom: 1px solid rgba(0, 0, 0, 0.12); }

.labelLeft {
  flex: 1;
  margin-right: 64px; }

.toRight {
  margin-right: 0; }

.specialChecklabel {
  display: block;
  padding: 1rem 0 1rem 0;
  margin: 1rem 0 1rem 0;
  border-top: 1px solid rgba(0, 0, 0, 0.12);
  border-bottom: 1px solid rgba(0, 0, 0, 0.12); }

.item {
  color: rgba(0, 0, 0, 0.54); }

.defaultItem {
  padding: 0.5rem 0.75rem 0.5rem 0.75rem;
  color: rgba(0, 0, 0, 0.54); }

.selected {
  color: rgba(0, 0, 0, 0.94);
  font-weight: 700; }

.highlighted {
  color: white;
  background: #008380; }

.imageInput {
  appearance: none;
  border-radius: 3px;
  padding: 0.75rem 0.75rem 0.75rem 0.75rem;
  background: #008380;
  color: white; }

.slide {
  width: 320px;
  height: 320px; }

.imageSlider {
  position: absolute;
  background: #37A4A0;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  max-height: 320px; }

.fromRight {
  transform: translateX(100%);
  animation: translateImage 0.8s 1 cubic-bezier(0.8, 0, 0.2, 1); }

.fromLeft {
  transform: translateX(-100%);
  animation: translateImageReverse 0.8s 1 cubic-bezier(0.8, 0, 0.2, 1); }

@keyframes translateImage {
  0% {
    transform: translateX(100%); }
  40% {
    transform: translateX(0%); }
  50% {
    transform: translateX(0%); }
  100% {
    transform: translateX(-100%); } }

@keyframes translateImageReverse {
  0% {
    transform: translateX(-100%); }
  40% {
    transform: translateX(0%); }
  50% {
    transform: translateX(0%); }
  100% {
    transform: translateX(100%); } }

.containerItem {
  background: #FD5C63; }

.CarouselControl {
  outline: none;
  border: none;
  position: absolute;
  top: calc(50% - 16px);
  width: 64px;
  height: 32px;
  background: transparent;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  transition: all 0.28s;
  color: rgba(255, 255, 255, 0.54);
  font-size: 13px;
  line-height: 16px;
  font-weight: 500;
  text-transform: uppercase; }
  .CarouselControl:hover {
    background: #008380;
    color: white;
    transition: all 0.28s; }

.controlLeft {
  composes: CarouselControl;
  left: 20%; }

.controlRight {
  composes: CarouselControl;
  right: 20%; }

.iseng {
  clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%); }

.roundLeft {
  padding-left: 6px; }

.roundRight {
  padding-left: 9px; }

.buttonRight {
  position: absolute;
  top: calc(180px - 20px);
  right: calc(0px - 20px); }

.buttonLeft {
  position: absolute;
  top: calc(180px - 20px);
  left: calc(0px - 20px); }

.imageCarousel {
  border-radius: 3px; }

.column {
