.root {
  cursor: pointer;
  border-radius: 9999px;
  display: flex;
  outline: none;
  justify-content: flex-start;
  align-items: center;
  box-shadow: 0px 1px 1px 1px rgba(0, 0, 0, 0.14);
  border: 2px solid transparent;
  animation: showButton 0.4s 1 cubic-bezier(0.8, 0, 0.2, 1); }

@keyframes showButton {
  0% {
    transform: scale(0); }
  100% {
    transform: scale(1); } }

.small {
  width: 32px;
  height: 32px; }

.large {
  width: 40px;
  height: 40px; }

.primary {
  color: white;
  background: #008380; }

.primaryAlt {
  color: #008380;
  background: white; }

.secondary {
  color: rgba(0, 0, 0, 0.72);
  background: white;
  transition: all 0.36s; }
  .secondary:hover {
    color: rgba(0, 0, 0, 0.84);
    transition: all 0.28s; }

.secondaryAlt {
  color: rgba(0, 0, 0, 0.72);
  background: white; }

.transparentLight {
  color: rgba(255, 255, 255, 0.54);
  transition: all 0.36s; }
  .transparentLight:hover {
    color: white;
    transition: all 0.28s; }

.transparentDark {
  color: rgba(0, 0, 0, 0.72);
  transition: all 0.36s; }
  .transparentDark:hover {
    color: rgba(0, 0, 0, 0.84);
    transition: all 0.28s; }
