.root {
  appearance: none;
  margin: 0.75rem 0 0.75rem 0;
  width: 100%;
  outline: none;
  height: 4px;
  border-radius: 2px;
  background: rgba(255, 255, 255, 0.12); }
  .root ::-ms-fill-lower {
    background: white; }
  .root::-moz-focus-inner {
    border: 0; }
  .root::-webkit-slider-thumb {
    cursor: pointer;
    appearance: none;
    width: 28px;
    height: 28px;
    border-radius: 9999px;
    border: 1px solid rgba(0, 0, 0, 0.12);
    background: white;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.04); }
  .root::-ms-fill-lower {
    background: white !important; }
  .root::-moz-range-thumb {
    cursor: pointer;
    appearance: none;
    width: 28px;
    height: 28px;
    border-radius: 9999px;
    border: 1px solid rgba(0, 0, 0, 0.12);
    background: white;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.04); }
  .root::-moz-range-progress {
    background: white;
    border-radius: 9999px; }

:global input[type='range']::-moz-focus-outer {
  border: 0; }

:global ::-moz-focus-inner {
  border: 0; }
