.root {
  position: relative;
  box-sizing: border-box; }

.arrowContainer {
  position: absolute;
  right: 16px;
  top: calc(50% - 8px);
  user-select: none;
  pointer-events: none; }

.arrow {
  width: 16px;
  height: 16px;
  fill: rgba(0, 0, 0, 0.44);
  pointer-events: none;
  user-select: none; }

.field {
  width: 100%;
  border: 1px solid rgba(0, 0, 0, 0.08);
  border-radius: 2px;
  outline: none;
  transition: all 0.3s;
  box-sizing: border-box;
  appearance: none;
  background: white; }
  .field:focus, .field:active {
    border-color: #008380;
    appearance: none;
    outline: none;
    transition: all 0.28s; }

.small {
  composes: field;
  padding: 8px 11px;
  font-size: 14px;
  line-height: 18px; }

.normal {
  composes: field;
  padding: 11px 40px 11px 11px;
  font-size: 16px;
  line-height: 24px; }
