.root {
  width: 100%;
  border: 1px solid rgba(0, 0, 0, 0.08);
  border-radius: 2px;
  outline: none;
  transition: all 0.3s;
  box-sizing: border-box; }
  .root:focus, .root:active {
    border-color: #008380;
    transition: all 0.28s; }

.stack {
  display: block; }

.inline {
  display: inline; }

.critical {
  border-color: #D43242;
  background: rgba(237, 0, 1, 0.1); }

.positive {
  border-color: #008380;
  background: rgba(0, 131, 128, 0.1); }

.small {
  composes: root;
  padding: 8px 11px;
  font-size: 14px;
  line-height: 18px; }

.normal {
  composes: root;
  padding: 11px;
  font-size: 16px;
  line-height: 24px; }

.disabled {
  user-select: none;
  background: rgba(0, 0, 0, 0.04); }
  .disabled:focus, .disabled:active {
    transition: all 0.3s;
    border-color: rgba(0, 0, 0, 0.08);
    transition: all 0.28s; }
