@media (max-width: 599px) {
  .root {
    flex: 1;
    width: 100%;
    min-width: 100%;
    height: 50%; } }

@media (min-width: 600px) {
  .root {
    flex: 1;
    width: 100%;
    min-width: 100%;
    height: 50%; } }

@media (min-width: 900px) {
  .root {
    flex: unset;
    width: 55%;
    min-width: 55%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    padding-top: 3rem;
    padding-right: 3rem;
    padding-bottom: 3rem; } }

@media (min-width: 1224px) {
  .root {
    flex: unset;
    width: 55%;
    min-width: 55%;
    height: 100%; } }

@media (min-width: 1800px) {
  .root {
    flex: unset;
    width: 55%;
    min-width: 55%;
    height: 100%; } }

