.root {
  composes: container-bleed from global; }

.row {
  composes: row from global; }

.post {
  composes: col-sm-6 col-md-4 from global;
  padding: 12px;
  border-radius: 16px;
  transition: all 0.4s; }
  .post:hover {
    transition: all 0.28s;
    background: #FCE7DD; }

.imageContainer {
  width: 100%;
  height: 280px;
  margin-bottom: 16px;
  overflow: hidden;
  border-radius: 12px;
  transition: all 0.4s; }
  .imageContainer:hover {
    transform: translateY(-24px);
    transition: all 0.28s;
    box-shadow: 0px 24px 64px -24px #D43242; }
