.root {
  cursor: pointer;
  border-radius: 3px;
  outline: none;
  border-style: solid;
  letter-spacing: normal;
  font-family: "Masbul", "geomanist", "SF Pro Display", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  text-decoration: none; }

.link {
  display: inline-flex;
  align-items: center;
  justify-content: center; }

.stretch {
  width: 100%; }

.children {
  display: flex;
  justify-content: center;
  align-items: center; }

.childrenLink {
  display: inline-flex;
  justify-content: center;
  align-items: center; }

.icon {
  width: 20px;
  height: 20px;
  margin: 0 0.5rem 0 0;
  display: inline-flex;
  justify-content: center;
  align-items: center; }

.medium {
  font-weight: 500; }

.bold {
  font-weight: 700; }

.small {
  border-width: 1px;
  padding: 6px 16px;
  font-size: 14px;
  line-height: 22px; }

.large {
  border-width: 1px;
  padding: 11px 23px;
  font-size: 16px;
  line-height: 24px; }

.smallAlt {
  border-width: 2px;
  padding: 5px 15px;
  font-size: 14px;
  line-height: 22px; }

.largeAlt {
  border-width: 2px;
  padding: 10px 22px;
  font-size: 16px;
  line-height: 24px; }

.primary {
  composes: root;
  border-color: #008380;
  color: white;
  background-color: #008380;
  transition: all 0.4s ease; }
  .primary:hover {
    border-color: #00514F;
    transition: all 0.24s ease;
    background-color: #00514F; }
  .primary.disabled {
    color: rgba(255, 255, 255, 0.6);
    user-select: none; }

.primaryLight {
  composes: root;
  border-color: white;
  color: rgba(0, 0, 0, 0.72);
  background-color: white;
  transition: all 0.4s ease; }
  .primaryLight:hover {
    border-color: white;
    transition: all 0.24s ease;
    background-color: white; }
  .primaryLight.disabled {
    color: rgba(255, 255, 255, 0.54);
    user-select: none; }

.primaryAlt {
  composes: root;
  border-color: #FD5C63;
  color: white;
  background-color: #FD5C63;
  transition: all 0.4s ease; }
  .primaryAlt:hover {
    border-color: #D43242;
    transition: all 0.24s ease;
    background-color: #D43242; }
  .primaryAlt.disabled {
    color: rgba(255, 255, 255, 0.6);
    user-select: none; }

.primaryAltMedium {
  composes: primaryAlt medium; }

.primaryAltBold {
  composes: primaryAlt bold; }

.primaryMedium {
  composes: primary medium; }

.primaryMediumLight {
  composes: primaryLight medium; }

.primaryBold {
  composes: primary bold; }

.primaryBoldLight {
  composes: primaryLight bold; }

.primary_full {
  composes: primary;
  width: 100%;
  text-align: center; }

.secondary {
  composes: root;
  font-weight: 500;
  border-width: 1px;
  border-color: rgba(0, 0, 0, 0.54);
  background: transparent;
  color: rgba(0, 0, 0, 0.72);
  transition: all 0.4s ease; }
  .secondary:hover {
    background: rgba(0, 0, 0, 0.04);
    border-color: rgba(0, 0, 0, 0.84);
    transition: all 0.24s ease; }
  .secondary.disabled {
    color: rgba(0, 0, 0, 0.44);
    border-color: rgba(0, 0, 0, 0.12);
    user-select: none; }

.secondaryLight {
  composes: root;
  font-weight: 500;
  border-width: 1px;
  border-color: white;
  background: transparent;
  color: rgba(255, 255, 255, 0.84);
  transition: all 0.4s ease; }
  .secondaryLight:hover {
    color: white;
    border-color: white;
    transition: all 0.24s ease; }
  .secondaryLight.disabled {
    color: rgba(0, 0, 0, 0.44);
    border-color: rgba(0, 0, 0, 0.12);
    user-select: none; }

.secondaryAlt {
  composes: root;
  font-weight: 700;
  border-width: 2px;
  border-color: rgba(0, 0, 0, 0.54);
  background: transparent;
  color: rgba(0, 0, 0, 0.72);
  transition: all 0.4s ease; }
  .secondaryAlt:hover {
    background: rgba(0, 0, 0, 0.04);
    border-color: rgba(0, 0, 0, 0.84);
    transition: all 0.24s ease; }
  .secondaryAlt.disabled {
    color: rgba(0, 0, 0, 0.44);
    border-color: rgba(0, 0, 0, 0.12);
    user-select: none; }

.secondaryAltLight {
  composes: root;
  font-weight: 700;
  border-width: 2px;
  border-color: rgba(255, 255, 255, 0.71);
  background: transparent;
  color: white;
  transition: all 0.4s ease; }
  .secondaryAltLight:hover {
    color: white;
    border-color: white;
    transition: all 0.24s ease; }
  .secondaryAltLight.disabled {
    color: rgba(255, 255, 255, 0.54);
    border-color: rgba(255, 255, 255, 0.54);
    user-select: none; }

.outline {
  composes: root;
  color: rgba(0, 131, 128, 0.8);
  border-color: rgba(0, 131, 128, 0.8);
  background-image: linear-gradient(180deg, transparent 95%, #37A4A0 0);
  background-repeat: no-repeat;
  background-size: 0% 100%;
  transition: all 0.4s ease;
  background-color: transparent; }
  .outline:hover {
    transition: all 0.24s ease;
    color: #37A4A0;
    border-color: #37A4A0;
    background-size: 100% 100%; }

.outline_full {
  composes: outline;
  width: 100%; }
