.tableRow {
  width: 100%; }

.tableCell {
  padding: 0.75rem 0.75rem 0.75rem 0.75rem;
  text-align: left;
  vertical-align: top; }

.primary {
  font-weight: 600; }

.code {
  vertical-align: top;
  font-family: 'Roboto Mono', 'Fira Code', 'Operator Mono' !important; }

.required {
  vertical-align: middle;
  opacity: 0.72; }

.help {
  margin-top: 1.5rem;
  margin-bottom: 0.75rem;
  display: block;
  color: rgba(0, 0, 0, 0.54); }
